@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.formulario p {
  font-size: 1.4rem;
  color: #4a4949;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #4a4949;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .breve {
  height: 4rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}